<template>
  <div class="brand">
    <div class="brand__banner">
      <div class="brand__banner__box">
        <div class="brand__banner__box__title">{{ $t('m.brand.banner__title') }}</div>
        <!-- <div class="brand__banner__box__desc">{{ $t('m.brand.banner__desc') }}</div> -->
      </div>
      <div class="brand__banner__square"></div>
    </div>
    <div class="brand__content">
      <div class="brand__content__box">
        <div class="brand__content__box__leftpart">
          <div class="leftpart__img"><img src="https://index.cuican520.com/brand__b1.png" alt=""></div>
          <div class="leftpart__desc">{{ part2 }}</div>
          <div  style="margin-top: 34px;">
            <div class="brand__cuicanApp__code2">
              <div style="display: inline-block;">
                <img src="https://index.cuican520.com/code2.png" alt="">
                <div class="brand__cuicanApp__code2__scanme">{{ $t('m.brand.scanme') }}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="brand__content__box__rightpart">
          <div>
            <div class="rightpart__title">{{ t[0] }}</div>
            <div class="rightpart__desc">{{ $t('m.brand.banner__desc') }}</div>
          </div>
          <div>
             <img src="https://index.cuican520.com/brand__b2.png" alt="">
          </div>
          <div class="rightpart__info">
            <div class="rightpart__title">{{ t[1] }}</div>
            <div v-for=" n in cuicanApp" :key="n.key" class="rightpart__info__desc">{{ n }}</div>
          </div>
        </div>
      </div>
      <!-- <brand-title-item :title=t[0]></brand-title-item>
      <div class="brand__origin">
        <div class="brand__origin__box">
          <div class="brand__origin__box__part1">
            <img src="https://index.cuican520.com/brand1.png" alt="">
          </div>
          <div class="brand__origin__box__part2">{{ part2 }}</div>
          <div class="brand__origin__box__part3">
            <div class="brand__origin__box__part3__info">{{ part3 }}</div>
            <div class="brand__origin__box__part3__img"><img src="https://index.cuican520.com/brand2.png" alt=""></div>
          </div>
        </div>
        <div class="brand__origin__line"></div>
      </div>
      <brand-title-item :title=t[1]></brand-title-item>
      <div class="brand__cuicanApp">
        <div v-for=" n in cuicanApp" :key="n.key">{{ n }}</div>
        <div class="brand__cuicanApp__code2">
          <div style="display: inline-block;">
            <img src="https://index.cuican520.com/code2.png" alt="">
            <div class="brand__cuicanApp__code2__scanme">{{ $t('m.brand.scanme') }}</div>
          </div>
        </div>
      </div> -->
    </div>
  </div>
</template>
<script>
export default {
  name: 'brand',
  data () {
    return {
      brand: {},
      t: [],
      part2: '',
      part3: '',
      cuicanApp: []
    }
  },
  created () {
    this.brand = this.$t('m.brand')
    this.part2 = this.$t('m.brand.brand__origin.part2')
    this.part3 = this.$t('m.brand.brand__origin.part3')
    const b = this.brand.brand__title
    for (let i = 0; i < b.length; i++) {
      this.t.push(b[i].title)
    }
    const c = this.brand.cuicanApp
    for (let i = 0; i < c.length; i++) {
      this.cuicanApp.push(c[i].text)
    }
  },
  updated () {
    this.brand = this.$t('m.brand')
    this.part2 = this.$t('m.brand.brand__origin.part2')
    this.part3 = this.$t('m.brand.brand__origin.part3')
    const b = this.brand.brand__title
    for (let i = 0; i < b.length; i++) {
      this.t.push(b[i].title)
    }
    const c = this.brand.cuicanApp
    for (let i = 0; i < c.length; i++) {
      this.cuicanApp.push(c[i].text)
    }
  },
  methods: {
  }
}
</script>
<style lang="scss" scoped>
.brand {
  .brand__content {
    max-width: 800px;
    margin: 100px auto;
    img {
      width: 100%;
    }
    .brand__content__box {
      display: flex;
      justify-content: space-between;
      .brand__content__box__leftpart {
        width: 30%;
        .leftpart__img {
        }
        .leftpart__desc {
          text-indent: 2em;
          font-size: 14px;
          line-height: 2em;
          // color: #666;
          margin: 20px 0;
          max-height: 500px;
          overflow: scroll;
        }
      }
      .brand__content__box__rightpart {
        width: 100-35%;
        .rightpart__title {
          font-size: 16px;
          font-weight: 600;
        }
        .rightpart__desc {
          font-size: 13px;
          // color: #888;
          margin: 10px 0;
          line-height: 2em;
        }
        .rightpart__info {
          max-height: 500px;
          overflow: scroll;
          .rightpart__info__desc {
            line-height: 2em;
          }
        }
      }
    }
  }
  .brand__banner {
    height: 150px;
    background-color: #16232d;
    color: #fff;
    text-align: center;
    display: flex;
    align-items: center;
    vertical-align: middle;
    position: relative;
    .brand__banner__box {
      width: 55%;
      margin: 0 auto;
      line-height: 3em;
      .brand__banner__box__title {
        font-size: 24px;
      }
    }
  }
    .brand__cuicanApp__code2 {
      // text-align: center;
      img {
        vertical-align: bottom;
        width: 100%;
      }
      .brand__cuicanApp__code2__scanme {
        background-color: #273646;
        color: #fff;
        text-align: center;
      }
    }
    .brand__cuicanApp__code2>div {
      width: 85%;
      background-color: #273646;
      padding: 15px 15px 0 15px;
    }
}
</style>



